body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Rubik', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.flex-row-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

.flex-row-container > * {
  flex: 0 0 auto;
}

.flex-row-container .enable-shrink {
  flex-shrink: 1;
}

.flex-row-container .enable-grow {
  flex-grow: 1;
}

.flex-row-container.with-gutter {
  gap: 16px;
}

.items-container {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.items-container > *:not(:first-child) {
   margin-top: 24px;
}
